.carousel-section{
  width: 50%;
  position: relative;
  z-index: 1;

}
.carousel-container {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.carousel-slide {
  display: flex;
  transition: transform 0.5s ease;
}

.carousel-item {
  min-width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 300px; /* Adjust as needed */
  text-align: center;
  transition: transform 0.3s;
}

/* .card:hover {
  transform: scale(1.05);
} */

.card-image {
  width: 100%;
  height: auto;
}

.card-content {
  padding: 10px;
}

.card-title {
  font-size: 18px;
  margin: 10px 0;
}

.card-price {
  font-size: 16px;
  color: #555;
}

.carousel-controls {
  position: relative;
  top: 10px; /* Adjust the spacing from the card */
  display: flex;
  justify-content: center; /* Center the controls */
  align-items: center; /* Align items vertically */
  padding-bottom:1rem ;
}

.carousel-button {
  background: rgba(134, 21, 65, 0.842);
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  padding: 4px;
  transition: background 0.3s;
  margin: 0 10px; /* Space between buttons */
}

.carousel-button:hover {
  background: rgb(185, 64, 111);
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ccc;
  margin: 0 5px;
  cursor: pointer;
}

.indicator.active {
  background: rgb(75, 13, 37) ;
}

.scroll-down-indicator {
  position: relative;
  z-index: 987; /* or 'absolute' */
  left: 50%;
  transform: translateX(-50%);
  max-width: 200px;
  margin: 2rem 0;
  text-align: center;
  display: flex;
  gap: 10px;
  cursor: pointer;
  align-items: center;
}


.scroll-text {
  font-size: 1rem;
  font-weight: bold;
  color: rgba(0, 119, 255, 0.774);
  
}

.scroll-arrow {
  width: 20px;
  height: 20px;
  border-bottom: 2px solid #333;
  border-right: 2px solid #333;
  transform: rotate(45deg);
  animation: scroll-down 1.5s infinite;
}

@keyframes scroll-down {
  0% {
      transform: rotate(45deg) translateY(0);
  }
  50% {
      transform: rotate(45deg) translateY(10px);
  }
  100% {
      transform: rotate(45deg) translateY(0);
  }
}
